import React from 'react';
import { useDispatch } from 'react-redux';
import { startAsociar, startEliminarRRPP, startSetRrppActive } from '../../actions/auth';
import {isMobile} from 'react-device-detect';
import { useHistory } from 'react-router-dom';
import { uiOpenModal } from '../../actions/ui';

export const RppItems = ({rpps,loading}) => {
  const dispatch = useDispatch();
  const history = useHistory();
  if(loading){
    return <h2>Cargando...</h2>
  }
  const handleGoAsociar = (uid) => {
      dispatch(startAsociar(uid));
      dispatch(uiOpenModal());
  };
  const handleGoEditar = (rid) => {
    dispatch(startSetRrppActive(rid));
    setTimeout(()=>{
      history.push(`/editarrrpp/${rid}`);
    },500);
  };
  const handleGoEliminar = (uid) => {
    dispatch(startEliminarRRPP(uid));
    history.push('/controlrpp');
  };
  return(
  <>
    {rpps && <table className={isMobile ? "d-block overflow-scroll table table-bordered table-striped":"table table-bordered table-striped"}>
  <thead className="bg-dark text-light">
    <tr>
      <th className="text-center small" scope="col">DNI</th>
      <th className="text-center small" scope="col">Nombre</th>
      <th className="text-center small" scope="col">Apellido</th>
      <th className="text-center small" scope="col">Estado</th>
      <th className="text-center small" scope="col">Email</th>
      <th className="text-center small" scope="col">Password</th>
      <th className="text-center small" scope="col">Rol</th>
      <th className="text-center small" scope="col">C. RRPP</th>
      <th className="text-center small" scope="col">C. Propia Vta.</th>
      <th className="text-center small" scope="col">Total Entradas</th>
      <th className="text-center small" scope="col">Total Favor</th>
      <th className="text-center small" scope="col">Editar</th>
      <th className="text-center small" scope="col">Eliminar</th>
      <th className="text-center small" scope="col">Asociar Evento</th>
    </tr>
  </thead>
  <tbody>
  {rpps.map(item => {
      return (
        <tr key={item.uid}>
          <td className="text-center small">{ item.dni }</td>
          <td className="text-center small">{ item.nombre }</td>
          <td className="text-center small">{ item.apellido }</td>
          <td className="text-center small">{ item.estado ? <i className="fas fa-check"></i> : <i className="fas fa-close"></i>  }</td>
          <td className="text-center small" style={{maxWidth:"150px",wordWrap:"break-word"}}>{ item.email }</td>
          <td className="text-center small">{ item.passwordShow ? item.passwordShow : "-" }</td>
          <td className="text-center small">{ item.rol }</td>
          <td className="text-center small">{ item.deuda?item.deuda:"-" }</td>
          <td className="text-center small">{ item.deuda?item.deuda:"-" }</td>
          {item.eventos.length>0?item.eventos.map(evento => {
              return  <td key={item.uid} className="text-center small">{ evento.totalEntradas?evento.totalEntradas:"-" }</td>
          })
          :<td className="text-center small">-</td>}
          {item.eventos.length>0? item.eventos.map(evento => {
              return  <td key={item.uid} className="text-center small">{ evento.totalEntradas-evento.deuda }</td>
          })
          :<td className="text-center small">-</td>}
          <td className="text-center small"><button className='btn btn-warning btn-sm' onClick={()=>handleGoEditar(item.uid)}><i className="fas fa-pen-to-square"></i></button></td>
          <td className="text-center small"><button className='btn btn-danger btn-sm' onClick={()=>handleGoEliminar(item.uid)}><i className="fas fa-trash-alt"></i></button></td>
          <td className="text-center small"><button className='btn btn-primary btn-sm' onClick={()=>handleGoAsociar(item.uid)}><i className="fas fa-link"></i></button></td>
        </tr>
      );
    })}
  </tbody>
</table>}
</> );
};
